export default {
  name: 'tabs-tabs',

  data () {
    return {
      mobile: false
    }
  },

  computed: {
    classes () {
      return {
        'tabs__tabs--mobile': this.mobile
      }
    }
  },

  methods: {
    scrollLeft () {
      this.$refs.container.scrollLeft -= 50
    },

    scrollRight () {
      this.$refs.container.scrollLeft += 50
    }
  }
}



