import Toggleable from '../../mixins/toggleable'

export default {
  name: 'chip',

  mixins: [Toggleable],

  data () {
    return {
      isActive: true
    }
  },

  props: {
    close: Boolean,

    label: Boolean,

    outline: Boolean,

    small: Boolean
  },

  computed: {
    classes () {
      return {
        'chip--label': this.label,
        'chip--outline': this.outline,
        'chip--small': this.small,
        'chip--removable': this.close
      }
    }
  }
}



