export default {
  name: 'button-dropdown',

  data () {
    return {
      isActive: false,
      inputValue: this.value || { text: this.placeholder },
      editableValue: ''
    }
  },

  props: {
    editable: Boolean,

    options: {
      type: Array,
      default: () => []
    },

    maxHeight: {
      type: [String, Number],
      default: 200
    },

    overflow: Boolean,

    placeholder: {
      type: String,
      default: 'Select'
    },

    segmented: Boolean,

    value: {
      required: false
    }
  },

  computed: {
    classes () {
      return {
        'btn-dropdown--editable': this.editable,
        'btn-dropdown--overflow': this.overflow || this.segmented || this.editable,
        'btn-dropdown--segmented': this.segmented
      }
    },

    computedItems () {
      if (this.editable) {
        return this.options
      }

      if (this.index !== -1 &&
        (this.overflow || this.segmented || this.editable)
      ) {
        return this.options.filter((obj, i) => i !== this.index)
      }

      return this.options
    },

    index () {
      return this.options.findIndex(i => i === this.inputValue)
    }
  },

  mounted () {
    this.editableValue = this.inputValue.text
  },

  watch: {
    isActive () {
      if (this.editable) {
        if (!this.isActive) {
          this.$refs.input.blur()
        }
      }
    },

    inputValue () {
      this.$emit('input', this.inputValue)
    },

    value () {
      if (typeof this.value === 'string') {
        return (this.inputValue = { title: this.value })
      }

      this.inputValue = this.value
    }
  },

  methods: {
    toggle (active) {
      this.isActive = active
    },

    updateValue (obj) {
      if (typeof obj === 'string') {
        obj = { title: obj }
      }

      this.inputValue = obj

      this.$emit('input', obj)

      if (this.editable) {
        this.editableValue = obj.text
        this.inputValue = obj
      }

      this.isActive = false
    }
  }
}



