export default {
  name: 'select',

  data () {
    return {
      focused: false,
      inputValue: null
    }
  },

  props: {
    defaultDisabled: {
      type: Boolean,
      default: true
    },

    defaultText: {
      type: String,
      default: 'Select...'
    },

    disabled: Boolean,

    id: {
      type: String,
      value: ''
    },

    label: {
      type: String,
      value: ''
    },

    multiple: Boolean,

    name: {
      type: String,
      value: ''
    },

    options: {
      type: Array,
      default: () => []
    },

    value: {
      required: false
    }
  },

  computed: {
    classes () {
      return {
        'input-group--dirty': true
      }
    }
  },

  watch: {
    inputValue () {
      if (this.multiple) {
        this.$refs.options.filter(i => i.selected = this.inputValue.includes(i.value))
      } else {
        this.$refs.select.value = this.inputValue
      }
    },

    value () {
      this.inputValue = this.value
    }
  },

  mounted () {
    if (this.value) {
      this.inputValue = this.value
    }
  },

  methods: {
    updateValue () {
      if (this.multiple) {
        this.$emit('input', this.$refs.options.filter(i => i.selected).map(i => i.value))
      } else {
        this.$emit('input', this.$refs.select.value)
      }
    }
  }
}



