export default {
  name: 'text-input',

  data () {
    return {
      focused: false,
      inputValue: this.value ? this.value.toString() : null
    }
  },

  computed: {
    classes () {
      return {
        'input-group--focused': this.focused,
        'input-group--dirty': this.value || this.placeholder
      }
    }
  },

  props: {
    disabled: Boolean,

    label: String,

    id: String,

    name: String,

    placeholder: String,

    required: Boolean,

    type: {
      default: 'text'
    },

    value: {
      required: false
    }
  },

  watch: {
    value (value) {
      this.inputValue = value
    },

    inputValue () {
      this.$emit('input', this.inputValue)
    }
  },

  methods: {
    blur () {
      this.focused = false
    },

    focus () {
      this.focused = true
    },

    updateValue (e) {
      this.inputValue = e.target.value
    }
  }
}



